import React, { Component } from 'react';
import LoginModal from '../../modals/components/LoginModal/LoginModal.component';
import RegisterModal from '../../modals/components/RegisterModal/RegisterModal.component';
import Icon from '../../ui-kit/icon/icon.component';
import Button from '../../ui-kit/button/button.component';
import iconLogo from '../../../resources/svg/icon-logo.svg';
import iconToggle from '../../../resources/svg/icon-shape.svg';
import iconUK from '../../../resources/svg/icon-uk.svg';
import css from './header.component.scss';

class Header extends Component {
  /*
  // old format
  constructor(props) {
    super(props);
    this.state = {
      loginModalIsOpen: false,
    };
    this.openLoginModal = this.openLoginModal.bind(this);
  }
  */
  state = {
    loginModalIsOpen: false,
    registerModalIsOpen: false,
  }
  render() {
    const { loginModalIsOpen, registerModalIsOpen } = this.state;
    /*
    // eslint-disable-next-line
    console.log(` Login Modal Open : ${loginModalIsOpen}`);
    // eslint-disable-next-line
    console.log(` Register Modal Open : ${registerModalIsOpen}`);
    */
    return (
      <header className={css.header}>
        <Icon name={iconLogo} className={css.logo} />
        <Button className={css.toggle}>
          <Icon name={iconToggle} className={css.iconTogle} />
        </Button>
        <div className={css.profile}>
          <Button className={css.button}>
            List your Vehicle
          </Button>
          <Button
            className={css.button}
            onClick={this.openLoginModal}
          >
            Log In
          </Button>
          <Button className={css.button} onClick={this.openRegisterModal}>
            Register for free
          </Button>
          <Icon name={iconUK} className={css.icon} />
        </div>
        <LoginModal
          isOpen={loginModalIsOpen}
          closeModal={this.closeLoginModal}
        />
        <RegisterModal
          isOpen={registerModalIsOpen}
          closeModal={this.closeRegisterModal}
        />
      </header>
    );
  }
  /*
  // old format
  openLoginModal() {
    this.setState({
      loginModalIsOpen: true,
    });
  }
  */
  openLoginModal = () => {
    // eslint-disable-next-line
    console.log("openLoginModal");
    this.setState({
      loginModalIsOpen: true,
    });
  }
  closeLoginModal = () => {
    // eslint-disable-next-line
    console.log("closeLoginModal");
    this.setState({
      loginModalIsOpen: false,
    });
  }
  openRegisterModal = () => {
    // eslint-disable-next-line
    console.log("openRegisterModal");
    this.setState({
      registerModalIsOpen: true,
    });
  }
  closeRegisterModal = () => {
    // eslint-disable-next-line
    console.log("closeRegisterModal");
    this.setState({
      registerModalIsOpen: false,
    });
  }
}

export default Header;


