import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Modal from '../../../ui-kit/Modal/Modal.component';
import Button from '../../../ui-kit/button/button.component';
import Icon from '../../../ui-kit/icon/icon.component';
import Input from '../../../ui-kit/input/input.component';
import iconClose from '../../../../resources/svg/icon-close.svg';
import css from './LoginModal.component.scss';

class LoginModal extends Component {
  render() {
    const { closeModal } = this.props;
    return (
      <Modal
        {...this.props}
      >
        <div className={css.closeContainer}>
          <Button
            tag="button"
            onClick={closeModal}
          >
            <Icon name={iconClose} className={css.close} />
          </Button>
        </div>
        <form>
          <Input
            className={css.inputE}
            titleClassName="blueTitle"
            title="Your Email"
            placeholder="Enter Your Email"
          />
          <Input
            className={css.inputP}
            placeholder="Your Password"
          />
          <Button
            tag="button"
            type="submit"
            className={css.logButton}
          >
            <span className={css.socText}>
              Log In
            </span>
          </Button>
        </form>
        <div className={css.forgotContainer}>
          <Button className={css.forgot}>
              Forgot Password?
          </Button>
        </div>
        <div className={css.line} />
        <div className={css.questionContainer}>
          <span className={css.question}>Dont have an account yet?</span>
          <Button className={css.register}>
            Register
          </Button>
        </div>
      </Modal>
    );
  }
}

LoginModal.propTypes = {
  closeModal: PropTypes.func,
};

export default LoginModal;


